	module.exports = {"data":{"markdownRemark":{"html":"<p>私はいくつかの記事に集中し、「革命後の社会における政府」と書く準備をしました。この本はどのように書かれ、うまく設計され、新しいエントリーポイントが得られますか？中国の発展にはこうした研究が必要なだけでなく、革命主義政府の発展から、世界の中国や他の社会の発展を理解することも有益である。そのようなテーマを書く方法を見てください。</p>\n<p>基本的な考え方は、革命後の社会における国家機械の破壊と国家機械の再構築と政治制度の役割の観点から、「社会的資源」の観点から議論することである。これらは革命後の社会における最も重要な問題です。革命後、社会は特別な社会であり、人類の歴史によく現れる社会ですが、社会的な形態ではなく革命そのものに起因する特殊な社会構造です。問題は、革命後の社会が秩序ある社会になって新たな秩序を確立することです。これは革命後の社会が安定した社会に移行するための最も基本的な方法です。これが解決しなければ、革命後の社会は不安定な社会です。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0080.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0080.md","frontmatter":{"title":"4月24日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0080.md absPath of file >>> MarkdownRemark"}}

